/*Globales*/
* {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  box-sizing: border-box;
}
.title {
  color: rgba(68, 255, 236, 0.808);
  text-align: center;
  margin: 60px 0 45px 0;
  font-family: "Rubik", sans-serif;
  font-size: 24px;
}

/*Header*/
.header__menu {
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0px;
  width: 100%;
  height: 60px;
  background-color: #099d8ebd;
  z-index: 999;
}
.header__menu__items {
  display: inline-block;
  margin: 10px;
  font-weight: bold;

  /* arreglar que las letras no se salgan del borde */
}
.logo {
  margin: 5px;
}
.header__menu__nav {
  position: relative;
  top: 35px;
}

.link {
  color: #a1ca9b;
  text-decoration: none;
  text-transform: uppercase;
  /* font-size: 16px; */
}
.link:hover {
  color: #eb636b;
}

.asterisco {
  color: #14d9c5c9;
}

@media all and (max-width: 768px) {
  .header__menu {
    justify-content: center;
    position: fixed;
    width: 100vw;
    height: 60px;
    background-color: #099d8ebd;
  }
  .header__menu__nav {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    position: fixed;
    width: 100vw;
    height: 35px;
    top: 60px;
    background-color: #14d9c5c9;
  }
  .header__menu__items {
    color: white;
    margin: 10px;
    font-weight: bold;
  }
  .link {
    color: white;
    text-decoration: none;
  }
  .link:hover {
    color: #eb636b;
  }
  ul {
    padding: 0%;
  }
}

@media all and (min-width: 768px) and (max-width: 1200px) {
  .header__menu {
    justify-content: space-between;
    position: fixed;
    width: 100vw;
    height: 50px;
    background-color: #099d8d;
  }

  .header__menu__nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: white;
    position: fixed;
    width: 100vw;
    height: 50px;
    top: 60px;
    background-color: #14d9c5c9;
  }
  .link {
    color: white;
    text-decoration: none;
  }
}

/*Hero*/

.page__contact .hero-section {
  background-image: url("../images/contact_slide.jpg");
}

@media all and (max-width: 768px) {
  .hero-section {
    height: 200px;
  }
}
@media all and (min-width: 768px) and (max-width: 1200px) {
  .hero-section {
    height: 361px;
  }
}

@media all and (min-width: 1200px) {
  .hero-section {
    height: 490px;
  }
}

